*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body{
  font-family: 'poppins',sans-serif;
}
.container{
  max-width: 500px;
  margin:30px auto;
  overflow: auto;
  min-height: 300px;
  color:#000;
  border: 1px solid steelblue;
  padding: 30px;
  border-radius: 5px;
}
.header{
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}
.btn{
  display: inline-block;
  background-color:#000;
  color:#fff;
  border:none;
  padding: 10px 20px;
  margin: 5px;
  border-radius: 5px;
  cursor:pointer;
  text-decoration: none;
  font-family: 15px;
  font-family: inherit;

}
.btn{
  outline: none;

}
.btn:active{
transform: scale(0.98);
}
.btn-block{
  display:block;
}
.event{
  background-color: #f4f4f4;
  margin: 5px;
  padding: 10px 20px;
  cursor:pointer;

}
.event.reminder{
  border-left:5px solid green;
}
.event.notreminder{
  border-left:5px solid red;
}


.event h3{
  display :flex;
  align-items: center;
  justify-content: space-between;
}
.add-form{
  margin-bottom: 40px;
  border: 1px solid black;
  border-radius: 8px;
  padding: 4px;
  display: flex;
  flex-direction: column;
}
.form-control{
  margin:20px 0;
}
.form-control label{
  display:block;
}
.form-control-check input{
flex:2;
height: 20px;
}
footer{
  margin-top: 30px;
  text-align:center;
}
